import { Component, h, Prop, State, Watch, Method } from '@stencil/core';
import { common } from '../../global/data/common';


interface SelectOption {
    name: string;
    value: string;
}


@Component({
      tag: 'ks-input-select-dynamic'
})
export class InputSelectDynamic {

    @Prop() name: string;
    @Prop() label: string;

    @Prop() api: string;
    @Prop({mutable: true, reflect: true}) apiData = "";
    @Prop() selectedId: string;

    @Prop({mutable: true}) lightUp: boolean = false;

    @State() data = new Array<SelectOption>();

    @State() loading: boolean = false;
    @State() message: string = "";

    @Watch('apiData')
    async FetchData() {
        const strings = common.get("translations");

        var headers = new Headers();
        headers.append('pragma', 'no-cache');
        headers.append('cache-control', 'no-cache');

        this.loading = true;
        this.message = "";
        await fetch(this.api + this.apiData, {
            method: 'GET',
            headers: headers,
            credentials: "same-origin"
        })
            .then(response => {
                if(!response.ok)
                    throw response.status;

                return response.json()
            })
            .then(data => {
                this.data = data;
            })
            .catch(error => {
                let message = error;

                if(Number.isInteger(error))
                    message = strings.inputConnectionError + error;

                else if(!navigator.onLine)
                    message = strings.inputInternetConnectionError;

                this.message = message;
            });
    
        this.loading = false;
    }

    @Method()
    async Validate() {
        this.lightUp = false;
    }

    @Method()
    async IsValid() {
        return !this.loading && this.message == "";
    }

    componentWillLoad() {
        this.FetchData();
    }

    Decode(input) {
        var doc = new DOMParser().parseFromString(input, "text/html");
        return doc.documentElement.textContent;
    }

	render() {            
        const strings = common.get("translations");

        return(
            <div class="uk-position-relative">
                <ks-input-select name={this.name} label={this.label} error={this.message != ""} light-up={this.lightUp}>
                    {this.data.map( (item: SelectOption)=>
                        <option selected={this.selectedId == item.value ? true : false} value={item.value}>{this.Decode(item.name)}</option>
                    )}
                </ks-input-select>

                {this.loading ?
                    <div class="uk-position-absolute uk-animation-fade"
                         style={{top: "20px", left: "0px", width: "100%", height: "50px", backgroundColor: "rgba(255, 255, 255, 0.7)"}}>

                        <div uk-spinner class="uk-position-center"></div>
                    </div>
                : null}

                {this.message != "" ?
                    <div class="uk-position-absolute"
                         style={{top: "20px", left: "0px", width: "100%", height: "50px", backgroundColor: "rgba(255, 255, 255)"}}>

                        <p class="uk-position-center uk-text-danger">
                            {this.message} <a onClick={()=>this.FetchData()}>{strings.inputRefresh}</a>
                        </p>
                        
                    </div>
                : null}
            </div>
        );
    }
}
